.says {
  max-width: 1920px;
  width: 100%;
  height: 920px;
  background-color: #f9fbfb;
}
.says-us {
  width: 617px;
  margin: 0 auto;
}
.says__title {
  opacity: 0.9;
  font-family: "Montserrat", sans-serif;
  font-size: 38px;
  line-height: 0.74;
  letter-spacing: 2.28px;
  text-align: center;
  color: #000000e6;
  margin: 0 auto;
  padding-top: 130px;
  text-transform: uppercase;
}
.says__text {
  opacity: 0.62;
  font-family: "Raleway", sans-serif;
  font-size: 18px;
  line-height: 1.56;
  letter-spacing: 2px;
  text-align: center;
  color: #0000009e;
  margin: 0 auto;
  padding-top: 17px;
  padding-bottom: 120px;
  text-transform: uppercase;
}

.container-says {
  display: flex;
  justify-content: space-around;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}
.slider-wrapper {
  position: relative;
}

.block__says {
  position: absolute;
  z-index: 1;
  display: none;
  top: 0px;
}

.block__says.active {
  position: relative;
  z-index: 1;
  display: block;
}

.dots-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  right: 20%;
  margin-top: 38.7%;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 3px;
  background-color: #d0d0d0;
  margin-right: 2px;
}

.dot.active {
  width: 11px;
  height: 10px;
  border-radius: 3px;
  background-color: #71f6eb;
}

.background__says {
  position: absolute;
  z-index: -1;
  width: 393px;
  height: 362px;
  border-radius: 3px;
  background-color: #ecedef;
  top: 40px;
  right: -16px;
}
.block__information {
  margin-top: 40px;
  width: 666px;
  height: 362px;
  border-radius: 3px;
  background-color: #eaebed;
}

.block__information-text {
  margin-top: 80px;
  opacity: 0.95;
  font-family: "Montserrat", sans-serif;
  font-size: 15px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.87;
  letter-spacing: 0.9px;
  text-align: center;
  color: #787878f3;
  margin-left: auto;
  text-transform: uppercase;
  & b {
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 1.2px;
    color: #000000f3;
    text-transform: uppercase;
  }
}

.block__information-title {
  width: 515px;
  height: 105px;
  opacity: 0.7;
  font-family: "Raleway", sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.69;
  letter-spacing: 0.96px;
  text-align: center;
  color: #000000b3;
  margin-left: 75px;
}
.rating-mini {
  display: inline-block;
  font-size: 0;
  margin-left: 285px;
  margin-top: 20px;
}
.rating-mini span {
  padding: 0;
  font-size: 20px;
  line-height: 1;
  color: lightgrey;
}
.rating-mini > span:before {
  content: "★";
}
.rating-mini > span.active {
  color: gold;
}

.arrows {
  display: flex;
  position: absolute;
  right: auto;
  margin-left: 535px;
  top: 6700px;
}
.arrow-prev,
.arrow-next {
  width: 40px;
  height: 40px;

  background-color: #71f6eb;
  margin: 0 4px;
  position: relative;
  cursor: pointer;
  transition: 0.3s ease;

  &:hover {
    box-shadow: 0 0 0 5px rgba(black, 0.5);
  }
  &:focus {
    box-shadow: 0 0 0 5px rgba(black, 0.5);
    outline: none;
  }
  &:active {
    box-shadow: 0 0 0 5px rgba(black, 1);
  }

  &::before,
  &::after {
    content: "";
    width: 40%;
    height: 4px;
    margin-top: -2px;
    background: #ffffff;
    position: absolute;
    top: 50%;
    border-radius: 4px;
  }
  &::before {
    transform: rotate(45deg);
  }
  &::after {
    transform: rotate(-45deg);
  }
}
.arrow-prev {
  &::before,
  &::after {
    left: 32%;
  }
  &::before {
    transform-origin: 0 0;
  }
  &::after {
    transform-origin: 0 100%;
  }
}
.arrow-next {
  &::before,
  &::after {
    right: 32%;
  }
  &::before {
    transform-origin: 100% 100%;
  }
  &::after {
    transform-origin: 100% 0;
  }
}

/* Спонсоры */

.sponsor {
  max-width: 1920px;
  width: 100%;
  margin-top: 100px;
  margin-bottom: 100px;
  text-align: center;
}

.sponsor-container {
  width: 1133px;
  height: 172px;
  margin: 0 auto;
}
